function GetRewardsofteamleader  (props){
  const classes = useStyles();
  console.log("in sgetrewardsofteamleaderrrr",props)
  
     
      if(Array.isArray(props.rewards.children))
      if(props.rewards.children.length>0)
        return (
          <React.Fragment>
           <TableContainer component={Paper}>
               
                  <Table size="small" aria-label="purchases">
                    <TableHead>
                      <TableRow>
                        <TableCell width="15%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right">תאריך הזמנה</TableCell>
                        <TableCell width="15%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right">ביצוע הזמנה</TableCell>
                        <TableCell width="20%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right">מתנה</TableCell>
                        <TableCell width="10%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right">סטטוס</TableCell>
                        <TableCell width="10%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right">בונוס</TableCell>
                        <TableCell width="10%"className={classes.bgc_grey} sx={{ fontWeight: 'bold', m: 1 }}align="right">ניתן להחזיר</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {props.rewards.children.map((reward) => (
                        <TableRow key={reward.attributes.DataFolNumber}>
                           <TableCell  align="right" component="th" scope="row">
                            {reward.attributes.Date}
                          </TableCell>
                          <TableCell  align="right" component="th" scope="row">
                            {reward.attributes.User}
                          </TableCell>
                          <TableCell  align="right">{reward.attributes.RewardDesc.replace(/&quot;/g,'"').replace(/&quot;/g,'"').replace(/&apos;/g,`'`)}</TableCell>
                          <TableCell align="right">{reward.attributes.Status}</TableCell>
                          <TableCell align="right">
                            {reward.attributes.Bonus==='True'?<CheckIcon></CheckIcon>:<CloseIcon></CloseIcon>}
                          </TableCell>
                          <TableCell align="right">
                            {reward.attributes.Returnable==='True'?<CheckIcon></CheckIcon>:<CloseIcon></CloseIcon>}
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                
                 </TableContainer> 
          </React.Fragment>
        );
        else
        return(<Box align="right"text-align="right" width="50%">
        <ErorList  err='not data' severity="error"></ErorList></Box>)
      else
      if(props.rewards){
      if(props.rewards.includes('Error')||props.rewards==='GetBonusDoc - Bonus document not found'){
        return(<Box align="right"text-align="right" width="50%">
        <ErorList  err={props.rewards} severity="error"></ErorList></Box>)
      }
      else{
        return(<Box align="right"text-align="right" width="50%">
        <ErorList  err='not data' severity="error"></ErorList></Box>)
      }
    }
      else
      return(
        <Box align="right"text-align="right" width="50%">מחפש נתונים</Box>
      )
      
  }
