export default function RewardsTable(props) {
  const classes = useStyles();
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell className={classes.boldtext}align="right">ראש צוות</TableCell>
            
            
          </TableRow>
        </TableHead>
        <TableBody text-align="right">
          {props.token.teamleaders.map((row) => (
            <Row text-align="right" key={row.attributes.TeamLeader} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

 	 	 

