class SumKodkodit extends React.Component {
  state = {
    buttonPressed: 0,
  };
  constructor(props) {
    super(props);
   
  }
  componentDidMount() {
      console.log(this.props.teamleaders)
      var c=0
      this.props.teamleaders.map((teamleader,index)=>{
        if(teamleader.attributes.DonationSum!="")
           c=c+parseFloat(teamleader.attributes.DonationSum)
          console.log(c)
        
      
    })
    c=parseInt(c)
    this.setState({buttonPressed:c})
    console.log("Component did mount", this.state.buttonPressed)
  }

  componentDidUpdate() {
    console.log("Component did update", this.state.buttonPressed)
  }

 
render() {
    return (
        <Card sx={{ minWidth: 275,maxWidth:20 }}>
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
        <CardContent>
         
          <Typography variant="h5" component="div">
           סה"כ עסקאות שבוצעו
          </Typography>
          <Typography sx={{ mb: 1.5 }} color="text.secondary">
            מתעדכן אחת לשעה
          </Typography>
          <Typography style={{color:red['A700']}} variant="h6">
          {this.state.buttonPressed.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
     <i class="fa fa-shekel" style={{fontSize:"15px",letterSpacing:'1em'}}></i>
          </Typography>
         
        </CardContent>
        
      </Card>
    );
  }
}

export default SumKodkodit;